"use client";

import React, { useState } from "react";
import Image from "next/image";
import logo from "../../public/Logo.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";
import * as fonts from "@/libs/fonts";

const Header: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const selectSection = (id: string) => {
    const section = document.getElementById(id);
    if (section) {
      section.scrollIntoView({ behavior: "smooth" });
    }
    setIsMenuOpen(false);
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <header
      id="screen-top"
      className="text-white absolute top-0 left-0 right-0 z-50 p-5 max-sm:bg-[#eb3752ba] max-md:p-3 max-sm:p-2 bg-transparent pl-[7%] pr-[7%] max-md:pl-[5%] max-md:pr-[5%] max-sm:pl-[2%] max-sm:pr-[2%]"
    >
      <div className="container mx-auto flex justify-between items-center">
        <a className="cursor-pointer">
          <Image src={logo} alt="Logo Secure Core Consulting" width={180} height={40} />
        </a>

        {/* Mobile menu icon */}
        <div
          className="max-sm:block hidden cursor-pointer"
          onClick={toggleMenu}
        >
          <FontAwesomeIcon
            icon={isMenuOpen ? faTimes : faBars}
            className="w-6 h-6"
          />
        </div>

        {/* Navigation menu */}
        <nav
          className={`${
            isMenuOpen ? "block" : "max-sm:hidden"
          } max-sm:absolute max-sm:top-[60px] max-sm:left-0 max-sm:w-full max-sm:bg-[#eb3752ba] max-sm:z-50`}
        >
          <ul className={`${fonts.funnel_sans.className} flex space-x-6 max-md:space-x-4 max-sm:space-x-0 max-sm:flex-col max-sm:items-center max-sm:space-y-4`}>
            <li>
              <a onClick={() => selectSection("about")} className="hover:underline cursor-pointer">
                ¿Quiénes somos?
              </a>
            </li>
            <li>
              <a onClick={() => selectSection("services")} className="hover:underline cursor-pointer">
                Servicios
              </a>
            </li>
            <li>
              <a onClick={() => selectSection("mission")} className="hover:underline cursor-pointer">
                Misión y Visión
              </a>
            </li>
            <li>
              <a onClick={() => selectSection("features")} className="hover:underline cursor-pointer">
                Valores Destacados
              </a>
            </li>
            <li>
              <a onClick={() => selectSection("contact")} className="hover:underline cursor-pointer">
                Contacto
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  );
};

export default Header;